import React, {Component} from 'react'
import { css } from '@emotion/core'
import { Table, Card, Button, Tag, Row, Col, Tooltip, Divider, Modal, Form, Input, Select } from 'antd'
// import { Link } from 'Config/routes'
const Option = Select.Option

class VehicleType extends Component {

  state = {
    loading: false,
    visible: false,
  }

  showModal = () => {
    this.setState({
      visible: true,
    })
  }

  handleOk = () => {
    this.setState({ loading: true })
    setTimeout(() => {
      this.setState({ loading: false, visible: false })
    }, 3000)
  }

  handleCancel = () => {
    this.setState({ visible: false })
  }

  render(){
    const { visible, loading } = this.state

    const columns = [{
      title: 'No',
      dataIndex: 'no'
    }, {
      title: 'Jenis Kendaraan',
      dataIndex: 'jenisKendaraan'
    }, {
      title: 'Status',
      dataIndex: 'status',
      render: status => (
        <span>
          {status.map(item => {
            let color = item.length > 5 ? 'geekblue' : 'green'
            if (item === 'Yes') {
              color = 'green'
            } else {
              color = 'red'
            }
            return <Tag color={color} key={item}>{item.toUpperCase()}</Tag>
          })}
        </span>
      )
    }, {
      title: 'Aksi', dataIndex: '', key: 'x', render: () => (
        <div>
          <Tooltip placement="bottom" title="Edit">
            <Button icon="edit" type="primary" ghost/> 
          </Tooltip>
          <Divider type="vertical" />
          <Tooltip placement="bottom" title="Hapus">
            <Button icon="delete" />
          </Tooltip>
        </div>
      )
    }]
    
    const data = [{
      key: '1',
      no: '1',
      jenisKendaraan: 'Roda 4',
      status: ['Yes']
    }, {
      key: '2',
      no: '2',
      jenisKendaraan: 'Roda 2',
      status: ['Yes']
    }, {
      key: '3',
      no: '3',
      jenisKendaraan: 'Roda 2 < 250cc',
      status: ['No']
    }, {
      key: '4',
      no: '4',
      jenisKendaraan: 'Roda 4',
      status: ['Yes']
    }]

    return(
      <Row>
        <Col span={14}>
          <Card css={css`
            border-radius: 8px;
            border-top: 4px solid rgb(24, 144, 255);
            box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
          `}>
            <div css={css`
              display: flex; 
              justify-content: flex-end; 
              margin-bottom: 1em; 
              button { 
              margin-right: 1em; 
              &:last-of-type{
                margin-right: 0;
              }
              }`}>
              <Tooltip placement="bottom" title="Export Excel">
                <Button css={css`
                border: 1px solid #ddd;
                &:hover {
                  border-color: green;
                }
                  img {
                    width: 20px;
                  }
                `}>
                  <img src="/static/icons/excel.png" />
                </Button>
              </Tooltip>
              <Tooltip placement="bottom" title="Print">
                <Button css={css`
                border: 1px solid #ddd;
                &:hover {
                  border-color: green;
                }
                  img {
                    width: 20px;
                  }
                `}>
                  <img src="/static/icons/print.png" />
                </Button>
              </Tooltip>
              <Button type="primary" onClick={this.showModal}>
                Tambah
              </Button>

              <Modal
                visible={visible}
                title="Title"
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={[
                  <Button key="back" onClick={this.handleCancel}>Batal</Button>,
                  <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
                    Simpan
                  </Button>,
                ]}
              >
                <Form css={css`display: grid; grid-gap: 1em;`}>
                  <div css={css`display: grid; grid-template-columns: 1fr 3fr; align-items:center;`}>
                    <label css={css`margin-right: 1em;`}>Jenis Kendaraan</label>
                    <Input />
                  </div>
                  <div css={css`display: grid; grid-template-columns: 1fr 3fr; align-items:center;`}>
                    <label css={css`margin-right: 1em;`}>Status</label>
                    <Select
                      placeholder="- Pilih Status -"
                    >
                      <Option value="pria">Aktif</Option>
                      <Option value="wanita">Tidak Aktif</Option>
                    </Select>
                  </div>
                </Form>
              </Modal>
            </div>
            <Table columns={columns} dataSource={data} />
          </Card>
        </Col>
      </Row>
    )
  }
}

export default VehicleType